import React from "react";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import Navigation from "./Navigation";
import Welcome from "./Welcome";
import AboutMe from "./AboutMe";
import SnakeGame from "./SnakeGame";
import WaterTracker from "./WaterTracker";
import StatsProject from "./StatsProject";
import FileCabinet from "./FileCabinet";
import MarkdownPreviewer from "./MarkdownPreviewer";
import ProjectNavbar from "./ProjectNavbar";

class App extends React.Component {

  render() {
    return(
      <div>
        {/* I used a HashRouter to make sure that pages still load correctly on refresh */}
        <Router>
          <Navigation />
          <Switch>
            <Route path="/" exact component={() =>
              <Welcome />
            } />

            <Route path="/projects/snake" exact>
              <div className="text-center project-section">
                <ProjectNavbar />
                <SnakeGame />
              </div>
            </Route>
            <Route path="/projects/water-tracker" exact>
              <div className="text-center project-section">
                <ProjectNavbar />
                <WaterTracker />
              </div>
            </Route>
            <Route path="/projects/stats-tools" exact>
              <div className="text-center project-section">
                <ProjectNavbar />
                <StatsProject />
              </div>
            </Route>
            <Route path="/projects/file-cabinet" exact>
              <div className="text-center project-section">
                <ProjectNavbar />
                <FileCabinet />
              </div>
            </Route>
            <Route path="/projects/markdown-previewer" exact>
              <div className="text-center project-section">
                <ProjectNavbar />
                <MarkdownPreviewer />
              </div>
            </Route>
            <Route path="/projects" exact replace>
              <div className="text-center project-section">
                <ProjectNavbar />
                <h1>Here are some recent projects I have completed:</h1>
                <SnakeGame />
                <WaterTracker />
                <StatsProject />
                <FileCabinet />
                <MarkdownPreviewer />
              </div>
            </Route>
            <Route path="/about-me" exact component={() =>
              <AboutMe />
            } />

          </Switch>
        </Router>
      </div>
    );
  }
};

export default App;
