import "../css/Technology.css";
import javaLogo from "../images/java-logo.png";
import cLogo from "../images/c-logo.png";
import reactLogo from "../images/react-logo.png";
import reduxLogo from "../images/redux-logo.svg";
import bootstrapLogo from "../images/bootstrap-logo.png";
import jQueryLogo from "../images/jQuery-logo.png";
import d3Logo from "../images/d3-logo.png";
import htmlLogo from "../images/html5.svg";
import vueLogo from "../images/vue-logo.png";
import nodeLogo from "../images/node-logo.png";
import expressLogo from "../images/express-logo.png";
import gitLogo from "../images/git-logo.png";
import mongodbLogo from "../images/mongodb-logo.png";
import $ from 'jquery';
import jQuery from 'jquery';
import "../fonts/Ubuntu-Light.ttf";
import React from 'react';

var click = true;


const animateImages = (e) => {
  if(click) {
    console.log(window.innerWidth);
    if(window.innerWidth < 500) {
      window.scroll({
        top: 930,
        left: 0,
        behavior: 'smooth'
      });
    }
    else {
      window.scroll({
        top: 540,
        left: 0,
        behavior: 'smooth'
      });
    }

    let num = Math.random() + 1;

    for(let x = 0.0; x <= 12.56; x += 0.1) {
      let topNum = x * Math.sin(x) * num;
      let sideNum = 5 * num * Math.cos( x) - 5 * num;
      $('#java').animate({left: 2 * sideNum + "px", top: 2 * topNum + "px"}, 10, "swing");
      $('#redux').animate({left: -sideNum + "px", top: topNum + "px"}, 10, "swing");
      $('#c').animate({left: -2 * sideNum + "px", top: -2 * topNum + "px"}, 10, "swing");
      $('#react').animate({left: sideNum + "px", top: topNum + "px"}, 10, "swing");
      $('#bootstrap').animate({left: -sideNum + "px", top: -topNum + "px"}, 10, "swing");
      $('#jquery').animate({left: -3 * sideNum + "px", top: -3 * topNum + "px"}, 10, "swing");
      $('#d3').animate({left: 2 * sideNum + "px", top: 2 * topNum + "px"}, 10);
      $('#html').animate({left: 3 * sideNum + "px", top: 3 * topNum + "px"}, 10);
      $('#vue').animate({left: 2 * sideNum + "px", top: 2 * topNum + "px"}, 10);
      $('#node').animate({left: -2 * sideNum + "px", top: -2 * topNum + "px"}, 10);
      $('#express').animate({left: 1.5 * sideNum + "px", top: -1.5 * topNum + "px"}, 10);
      $('#git').animate({left: -1.5 * sideNum + "px", top: 1.5 * topNum + "px"}, 10);
      $('#mongodb').animate({left: sideNum + "px", top: topNum + "px"}, 10);
    }
    $('.img-div').animate({left:"0px", top:"0px"}, 0.001);
    $('#click-me').remove();
  }
  else {
    jQuery.fx.off = true;
  }

  if(e.type === "click") {
    click = false;
  }
  window.removeEventListener('scroll', scrollEvent);
}

const scrollEvent = () => {
  click = true;
  $('#title-section')
    .append("<button id='click-me' class='btn btn-primary'>Click me</button>");
  $('#click-me').css({opacity: 1});

  window.removeEventListener('scroll', scrollEvent);
}

class Technology extends React.Component {

  componentDidMount() {
    window.addEventListener('scroll', scrollEvent);
  }


  render() {
    return(
      <div id="tech-section" className="text-center" onClick={animateImages}>
        <div id="title-section">
          <h2 id="tech-intro" className="section-title">I have experience with the following technologies:</h2>
        </div>
        <div id="tech-imgs" className="text-center">
          <div className="row justify-content-evenly">
            <div className="col-xs-1">

            </div>
            <div id="java" className="col-xs-3 img-div">
              <img id="java-logo" src={javaLogo} alt="java-logo" className="img-responsive" />
              <p className="img-label">Java</p>
            </div>
            <div id="c" className="col-xs-3 img-div">
              <img id="c-logo" src={cLogo} alt="c-logo" className="img-responsive" />
              <p id="c-label" className="img-label">C++</p>
            </div>
            <div id="express" className="col-xs-3 img-div">
              <img id="express-logo" src={expressLogo} alt="express logo" className="img-responsive" />
              <p id="express-label" className="img-label">ExpressJS</p>
            </div>
          </div>

          <div className="row justify-content-evenly">
            <div id="react" className="col-xs-3 img-div">
              <img id="react-logo" src={reactLogo} alt="react-logo" className="img-responsive"/>
              <p className="img-label">React</p>
            </div>
            <div id="redux" className="col-xs-3 img-div">
              <img id="redux-logo" src={reduxLogo} alt="redux-logo" className="img-responsive"/>
              <p className="img-label">Redux</p>
            </div>
            <div id="bootstrap" className="col-xs-3 img-div">
              <img id="bootstrap-logo" src={bootstrapLogo} alt="bootstrap-logo" className="img-responsive" />
              <p className="img-label">Bootstrap</p>
            </div>
            <div id="git" className="col-xs-3 img-div">
              <img id="git-logo" src={gitLogo} alt="git-logo" className="img-responsive" />
              <p className="img-label">Git</p>
            </div>
          </div>

          <div className="row justify-content-evenly">
            <div id="jquery" className="col-xs-3 img-div">
              <img id="jQuery-logo" src={jQueryLogo} alt="jQuery-logo" className="img-responsive" />
              <p className="img-label">jQuery</p>
            </div>
            <div id="d3" className="col-xs-3 img-div">
              <img id="d3-logo" src={d3Logo} alt="d3-logo" className="img-responsive" />
              <p className="img-label">D3</p>
            </div>
            <div id="html" className="col-xs-3 img-div">
              <img id="hmtl-logo" src={htmlLogo} alt="html-logo" className="img-responsive" />
              <p className="img-label">HTML5</p>
            </div>
            <div id="mongodb" className="col-xs-3 img-div">
              <img id="mongodb-logo" src={mongodbLogo} alt="mongodb-logo" className="img-responsive" />
              <p className="img-label">MongoDB</p>
            </div>
          </div>

          <div className="row justify-content-evenly">

            <div className="col-xs-1"></div>

            <div id="vue" className="col-xs-4 img-div">
              <img id="vue-logo" src={vueLogo} alt="vue-logo" className="img-responsive" />
              <p className="img-label">Vue</p>
            </div>

            <div className="col-xs-2"></div>

            <div id="node" className="col-xs-4 img-div">
              <img id="node-logo" src={nodeLogo} alt="node-logo" className="img-responsive" />
              <p className="img-label">Node.js</p>
            </div>
          </div>

        </div>
      </div>
    );
  }
};

export default Technology;
